import React from 'react'


const Book = ({ title, author, imgUrl }) => {

    const clickHandler = () => {
        alert('Clicked!');
    }
    const passingArgumentExample = (author) => {
        alert(`This is book of ${author}`);
    }
    return <article className='book'>
        <img src={imgUrl} alt='' />
        <h1>{title}</h1>
        <h3>{author}</h3 >
        <button type='button' onClick={clickHandler}>Reference Example</button>
        <button type='button' onClick={() => passingArgumentExample(author)}>Reference Example</button>
    </article>
}



export default Book;
